#score {
  position: absolute;
  top: 20px;
  left: 20px;
  color: white;
  font-size: 24px;
  font-weight: bold;
  z-index: 1000;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.8);
}
#restart {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 15px 30px;
  font-size: 18px;
  background: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  z-index: 1000;
}
#restart:hover {
  background: #45a049;
}
#splash {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  color: white;
  font-family: Arial, sans-serif;
}
#title {
  font-size: 4em;
  font-weight: bold;
  margin-bottom: 30px;
  text-align: center;
  text-shadow: 3px 3px 6px rgba(0,0,0,0.5);
}
#loading {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}
.letter {
  font-size: 2em;
  margin: 0 2px;
}
.period1, .period2, .period3 {
  animation: loading 1.5s infinite;
}
.period2 {
  animation-delay: 0.2s;
}
.period3 {
  animation-delay: 0.4s;
}
@keyframes loading {
  0%, 60%, 100% {
    opacity: 0;
  }
  30% {
    opacity: 1;
  }
}
#instructions {
  font-size: 1.2em;
  text-align: center;
  margin-bottom: 30px;
  line-height: 1.5;
}
#play {
  position: relative;
}
#pressPlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: transparent;
  border: none;
  cursor: pointer;
}
#play {
  background: #4CAF50;
  color: white;
  padding: 15px 30px;
  font-size: 1.5em;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}
#play:hover {
  background: #45a049;
}
